import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import BrowserSection from './form-components/browser-section';
import Language from './form-components/language';
import Os from './form-components/os';
import ProfileName from './form-components/profile-name';
import ProxyAndLocationSection from './form-components/proxy-and-location-section';
import Resolutions from './form-components/resolutions';
import StorageSection from './form-components/storage-section';
import { useSaveTemplate } from '../../../hooks';
import { templatesContext } from '../../../state';
import { TabWrapper } from '../menu/tab-wrapper';
import { PageTitle } from '../styles';

const Children = [
  Language,
  Resolutions,
  Os,
  ProfileName,
  ProxyAndLocationSection,
  BrowserSection,
  StorageSection,
];

const BrowserTemplate = (): JSX.Element => {
  const { selectedTemplate, updateSelectedTemplate } = useContext(templatesContext);

  const { t: translation } = useTranslation();
  const { saveTemplate } = useSaveTemplate(selectedTemplate, updateSelectedTemplate);

  const { browser } = selectedTemplate;

  const fieldProps = { browser, saveTemplate };

  return (
    <TabWrapper tabKey='default-profile-settings'>
      <PageTitle>
        {translation('defaultProfileSettings.title')}
      </PageTitle>
      {Children.map((Child, index) => <Child {...fieldProps} key={index} />)}
    </TabWrapper>
  );
};

export default BrowserTemplate;
