import React, { FC, useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { templatesContext } from '../../../../state';
import { refreshLocalUserDevice, useLocalUserDevice } from '../../../../state/local-user-device.atom';
import { getLanguageByBasedIp } from '../../../common/get-data-by-based-ip';
import LocalesManager from '../../../modalsComponents/components/locales-manager';
import { ITemplateFieldComponent } from '../interfaces/template-field-component.interface';
import { EditLanguagesSpan, InputText, ParamContainer, SubTitle } from '../styles';

const Language: FC<ITemplateFieldComponent> = (props) => {
  const localUserDevice = useLocalUserDevice();
  const { languages: realLocalLanguages = '', country: realLocalCountry = '' } = localUserDevice;

  const [localesModalVisible, setLocalesModalVisible] = useState<boolean>(false);

  const { updateSelectedTemplate, selectedTemplate } = useContext(templatesContext);

  const { t: translation } = useTranslation();

  useEffect(() => {
    refreshLocalUserDevice();
  }, []);

  const { browser, saveTemplate } = props;
  const languagesByBasedIp = getLanguageByBasedIp({
    realLocalLanguages,
    realLocalCountry,
  });

  const changeLanguage = (language: string): void => updateSelectedTemplate(
    {
      ...selectedTemplate,
      browser: {
        ...selectedTemplate.browser,
        language,
      },
    });

  const toggleLocalesListModal = (save: boolean, autoLang = true, newLanguage = ''): void => {
    if (save) {
      saveTemplate({ language: newLanguage, autoLang });
    }

    setLocalesModalVisible(false);
  };

  return (
    <>
      <ParamContainer style={{ marginTop: 20 }}>
        <div style={{ marginBottom: 10 }}>
          <SubTitle>
            {translation('language.title')}
          </SubTitle>
          <EditLanguagesSpan onClick={(): void => setLocalesModalVisible(true)}>
            {translation('base.edit')}
          </EditLanguagesSpan>
        </div>
        <InputText
          onBlur={(): void => saveTemplate({ language: browser.language })}
          value={browser.autoLang ? '' : browser.language}
          placeholder={translation('defaultProfileSettings.auto') || 'Auto'}
          onChange={(event): void => changeLanguage(event.target.value)}
          disabled={browser.autoLang}
        />
      </ParamContainer>
      <LocalesManager
        languages={browser.language}
        localesModalVisible={localesModalVisible}
        toggleLocalesListModal={toggleLocalesListModal}
        autoLang={browser.autoLang}
        languagesBasedOnIp={languagesByBasedIp}
      />
    </>
  );
};

export default Language;
