import { Switch as SwitchAntd } from 'antd';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { SwitchTitle } from './styles';
import { SwitchTooltip } from './switch-tooltip';
import { ITemplateFieldSwitchComponent } from '../../interfaces/template-field-component.interface';

const Switch: React.FC<ITemplateFieldSwitchComponent> = ({ isChecked, name, labelText, tooltipText, saveField }) => {
  const { t: translation } = useTranslation();
  const handleChange = (value: boolean): void => saveField({ [name]: value });

  return (
    <>
      <SwitchAntd
        checked={isChecked}
        onChange={(value): void => handleChange(value)}
      />
      <SwitchTitle>
        {translation(labelText)}
      </SwitchTitle>
      <SwitchTooltip>
        {translation(tooltipText)}
      </SwitchTooltip>
    </>
  );
};

export default Switch;
