import React from 'react';
import { useTranslation } from 'react-i18next';

import CustomDns from './custom-dns';
import { ITemplateFieldComponent } from '../../interfaces/template-field-component.interface';
import { Component, Title } from '../../styles';

const Children = [CustomDns];

const ProxyAndLocationSection: React.FC<ITemplateFieldComponent> = ({ browser, saveTemplate }) => {
  const { t: translation } = useTranslation();
  const fieldProps = { browser, saveTemplate };

  return (
    <>
      <Component>
        <Title>
          {translation('defaultProfileSettings.proxyAndLocation.title')}
        </Title>
      </Component>
      {Children.map((Child, index) => <Child {...fieldProps} key={index} />)}
    </>
  );
};

export default ProxyAndLocationSection;
