import { Select } from 'antd';
import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { browserUpdaterContext } from '../../../../../../state';
import { getSupportedOrbitaVersions } from '../../../../../common/api';
import { MajorVersionOption, normalizeOrbitaVersions } from '../../../../../common/orbita-browser';
import { addPrefix } from '../../../../../common/settings/browser/browser-version/utils';
import OrbitaVersionModals from '../../../../../modalsComponents/components/orbita-version-modals';
import { ITemplateFieldComponent } from '../../../interfaces/template-field-component.interface';
import { Component, Row, SubTitle } from '../../../styles';

const BROWSER_VERSION_COMPATIBLE = '109';

const BrowserVersionDefault: React.FC<ITemplateFieldComponent> = (props) => {
  const { browser, saveTemplate } = props;

  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isCompatible, setIsCompatible] = useState(true);
  const [isDowngrading, setIsDowngrading] = useState(true);
  const [orbitaVersionsSupported, setOrbitaVersionsSupported] = useState<string[]>([]);

  const { t: translation } = useTranslation();
  const { isOrbitaVersionCompatible } = useContext(browserUpdaterContext);

  const formatVersionLatest = (version: string): string =>
    `${translation('defaultProfileSettings.orbitaSelector.latest.title')} ${addPrefix(version)}`;

  const loadOrbitaVersions = async (): Promise<void> => {
    let { supportedOrbitaVersions } = await getSupportedOrbitaVersions();
    supportedOrbitaVersions = normalizeOrbitaVersions(supportedOrbitaVersions);
    setOrbitaVersionsSupported(supportedOrbitaVersions);
  };

  const handleSelect = (uaUserVersion: MajorVersionOption): void => saveTemplate({ uaUserVersion });
  const setBrowserVersionToCompatible = (): void => saveTemplate({ uaUserVersion: BROWSER_VERSION_COMPATIBLE });

  const handleModalClose = (): void => {
    if (!isCompatible) {
      setBrowserVersionToCompatible();
    }

    setIsModalVisible(false);
  };

  const handleModalSubmit = (): void => {
    setBrowserVersionToCompatible();
    setIsModalVisible(false);
  };

  const showModal = async (): Promise<void> => {
    const isVersionCompatible = await isOrbitaVersionCompatible(browser.uaUserVersion);
    setIsCompatible(isVersionCompatible);
    if (!isVersionCompatible) {
      setIsModalVisible(true);
    }
  };

  const getSelectValueNormalized = (): Exclude<MajorVersionOption, 'unset'> => {
    let valueNormalized = browser.uaUserVersion || 'unset';
    if (valueNormalized === 'unset') {
      valueNormalized = 'latest';
    }

    return valueNormalized;
  };

  const shouldDowngradeOrbitaVersion = async (): Promise<boolean> => {
    if (browser.uaUserVersion !== 'unset') {
      return false;
    }

    const isVersionCompatible = await isOrbitaVersionCompatible(browser.uaUserVersion);

    return !isVersionCompatible;
  };

  const downgradeToVersionCompatible = async (): Promise<void> => {
    const shouldDowngrade = await shouldDowngradeOrbitaVersion();
    if (shouldDowngrade) {
      setBrowserVersionToCompatible();
    }
  };

  useEffect(() => {
    loadOrbitaVersions();
    if (isDowngrading) {
      downgradeToVersionCompatible().finally(() => setIsDowngrading(false));
    }
  }, []);

  useEffect(() => {
    if (isDowngrading) {
      return;
    }

    showModal();
  }, [browser.uaUserVersion]);

  return (
    <>
      <Row>
        <Component>
          <SubTitle>
            {translation('defaultProfileSettings.orbitaSelector.title')}
          </SubTitle>
          <Select
            value={getSelectValueNormalized()}
            onSelect={handleSelect}
            style={{ width: '175px' }}
          >
            <Select.Option value='latest'>
              {formatVersionLatest(orbitaVersionsSupported[0])}
            </Select.Option>
            {orbitaVersionsSupported.map((versionSupported) => (
              <Select.Option value={versionSupported} key={versionSupported}>
                {addPrefix(versionSupported)}
              </Select.Option>
            ))}
          </Select>
        </Component>
      </Row>
      <OrbitaVersionModals.IncompatibleSelect
        visible={isModalVisible}
        handleSubmit={handleModalSubmit}
        handleCancel={(): void => setIsModalVisible(false)}
        handleClose={handleModalClose}
      />
    </>
  );
};

export default BrowserVersionDefault;
