import React from 'react';
import { useTranslation } from 'react-i18next';

import Bookmarks from './bookmarks';
import BrowserVersionDefault from './browser-version-default';
import StartUrl from './start-url';
import { ITemplateFieldComponent } from '../../interfaces/template-field-component.interface';
import { Component, Title } from '../../styles';

const Children = [BrowserVersionDefault, StartUrl, Bookmarks];

const BrowserSection: React.FC<ITemplateFieldComponent> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Component>
        <Title>
          {translation('defaultProfileSettings.browser.title')}
        </Title>
      </Component>
      {Children.map((Child, index) => <Child {...props} key={index} />)}
    </>
  );
};

export default BrowserSection;
