import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import {
  parseBookmarksObjToTxt,
  parseBookmarksTxtToObj,
} from '../../../../../../utils/bookmarks-utils';
import { BookmarksTextArea } from '../../../../../common/tabs/bookmarks/styles';
import { ITemplateFieldComponent } from '../../../interfaces/template-field-component.interface';
import { Component, SubTitle } from '../../../styles';

const Bookmarks: React.FC<ITemplateFieldComponent> = ({ browser, saveTemplate }) => {
  const [bookmarksText, setBookmarksText] = useState('');

  const { t: translation } = useTranslation();

  useEffect(() => {
    const parsedBookmarksObjectToTxt = parseBookmarksObjToTxt(browser.bookmarks);
    setBookmarksText(parsedBookmarksObjectToTxt);
  }, [browser.bookmarks]);

  const changeBookmarks = (event: any): void => {
    const text = event.target.value;
    setBookmarksText(text);
  };

  const saveBookmarks = (): void => {
    const parsedBookmarksObject = parseBookmarksTxtToObj(browser.bookmarks, bookmarksText);
    saveTemplate({ bookmarks: parsedBookmarksObject });
  };

  return (
    <Component>
      <SubTitle>
        {translation('personalArea.defaultProfile.bookmarks')}
      </SubTitle>
      <BookmarksTextArea
        canResize={true}
        value={bookmarksText}
        onChange={changeBookmarks}
        placeholder={translation('profileSettings.bookmarks.placeholder') || ''}
        onBlur={(): void => saveBookmarks()}
      />
    </Component>
  );
};

export default Bookmarks;
