import { Icon, Tooltip } from 'antd';
import React from 'react';

interface ISwitchTooltip {
  children: string | null;
}

export const SwitchTooltip: React.FC<ISwitchTooltip> = ({ children }) => (
  <Tooltip
    title={(
      <span style={{ fontSize: 12, color: 'var(--000000-antd-tooltip-opacity)' }}>
        {children}
      </span>
    )}
    overlayStyle={{ backgroundColor: 'var(--FFFFFF-personal-area-default-profile)', color: 'var(--000000-personal-area-default-profile)' }}
    placement='rightBottom'
    arrowPointAtCenter={true}
  >
    <Icon
      style={{
        marginTop: 6,
        marginLeft: 5,
        marginBottom: 6,
        opacity: 0.4,
      }}
      type='info-circle'
    />
  </Tooltip>
);
