import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { IBrowserParamsFromTemplates, IBrowserStorage } from '../../../../../state';
import { storageFields } from '../../constants';
import { ITemplateFieldComponent, ITemplateFieldSwitchComponent } from '../../interfaces/template-field-component.interface';
import { Component, Row, Title } from '../../styles';
import Switch from '../../utility-components/switch';

const StorageSection: React.FC<ITemplateFieldComponent> = ({ browser, saveTemplate }) => {
  const [storage, setStorage] = useState<IBrowserStorage>({});
  const { t: translation } = useTranslation();

  const saveField = (param: Partial<IBrowserParamsFromTemplates>): void => {
    setStorage((prev) => {
      const storageNew = { ...prev, ...param };
      saveTemplate({ storage: storageNew });

      return { ...prev, ...param };
    });
  };

  const childrenProps: ITemplateFieldSwitchComponent[] = storageFields.map((storageField) => ({
    ...storageField,
    isChecked: !!storage[storageField.name],
    saveField,
  }));

  useEffect(() => {
    setStorage(browser.storage);
  }, [browser]);

  return (
    <>
      <Component>
        <Title>
          {translation('defaultProfileSettings.storage.title')}
        </Title>
      </Component>
      {childrenProps.map((props) => (
        <Component key={props.name}>
          <Row>
            <Switch {...props} />
          </Row>
        </Component>
      ))}
    </>
  );
};

export default StorageSection;
