import { Icon, Tooltip } from 'antd';
import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import { templatesContext } from '../../../../../../state';
import { ITemplateFieldComponent } from '../../../interfaces/template-field-component.interface';
import { Component, InputText, Row, SubTitle } from '../../../styles';

const CustomDns: React.FC<ITemplateFieldComponent> = ({ browser, saveTemplate }) => {
  const templateCtx = useContext(templatesContext);
  const { t: translation } = useTranslation();

  const handleChange = (dns: string): void =>
    templateCtx.updateSelectedTemplate({
      ...templateCtx.selectedTemplate,
      browser: {
        ...templateCtx.selectedTemplate.browser,
        dns,
      },
    });

  return (
    <Component>
      <SubTitle>
        {translation('defaultProfileSettings.customDns.title')}
      </SubTitle>
      <Row>
        <InputText
          placeholder={translation('newProfile.advanced.dns.inputPlaceholder') || ''}
          value={browser.dns}
          onChange={({ target }): void => handleChange(target.value)}
          onBlur={(): void => saveTemplate({ dns: browser.dns })}
        />
        <Tooltip
          title={(
            <span style={{ fontSize: 12, color: 'var(--000000-antd-tooltip-opacity)' }}>
              {translation('newProfile.advanced.dns.help')}
            </span>
          )}
          overlayStyle={{ backgroundColor: 'var(--FFFFFF-personal-area-default-profile)', color: 'var(--000000-personal-area-default-profile)' }}
          placement='rightBottom'
          arrowPointAtCenter={true}
        >
          <Icon
            style={{
              marginTop: 10,
              marginLeft: 5,
              marginBottom: 10,
              opacity: 0.4,
            }}
            type='info-circle'
          />
        </Tooltip>
      </Row>
    </Component>
  );
};

export default CustomDns;
