import Popper from '@material-ui/core/Popper';
import { Icon, Select } from 'antd';
import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { RESOLUTIONS } from '../../../common/all-resolutions';
import { ITemplateFieldComponent } from '../interfaces/template-field-component.interface';
import { ParamContainer, Row, SelectAdvanced, SubTitle, TooltipBlock, TooltipContainer, TooltipIconContainer, TooltipInfo } from '../styles';

const Resolutions: FC<ITemplateFieldComponent> = (props) => {
  const [errorAnchorEl, setErrorAnchorEl] = useState<null | HTMLElement>(null);
  const { browser, saveTemplate } = props;

  const { t: translation } = useTranslation();

  const resolutionIndex = (resolution: typeof RESOLUTIONS[number]): number => RESOLUTIONS.indexOf(resolution);

  return (
    <Row>
      <ParamContainer>
        <SubTitle>
          {translation('defaultProfileSettings.minScreenResolution')}
        </SubTitle>
        <SelectAdvanced
          value={browser.minResolution}
          disabled={browser.os === 'android'}
          onChange={(resolution): void => saveTemplate({ minResolution: resolution as typeof RESOLUTIONS[number] })}
        >
          {RESOLUTIONS.map((resolution, index) => (
            <Select.Option key={resolution} value={resolution} disabled={index > resolutionIndex(browser.maxResolution)}>
              {resolution}
            </Select.Option>
          ))}
        </SelectAdvanced>
      </ParamContainer>
      <ParamContainer style={{ marginLeft: 30 }}>
        <SubTitle>
          {translation('defaultProfileSettings.maxScreenResolution')}
        </SubTitle>
        <SelectAdvanced
          value={browser.maxResolution}
          disabled={browser.os === 'android'}
          onChange={(resolution): void => saveTemplate({ maxResolution: resolution as typeof RESOLUTIONS[number] })}
        >
          {RESOLUTIONS.map((resolution, index) => (
            <Select.Option key={resolution} value={resolution} disabled={index < resolutionIndex(browser.minResolution)}>
              {resolution}
            </Select.Option>
          ))}
        </SelectAdvanced>
      </ParamContainer>
      <TooltipIconContainer
        onMouseLeave={(): void => setErrorAnchorEl(null)}
        onMouseEnter={(e): void => setErrorAnchorEl(e.currentTarget)}
      >
        <Icon
          style={{
            color: 'var(--E83E5E-personal-area-default-profile)',
          }}
          type='info-circle'
        />

      </TooltipIconContainer>

      <Popper
        open={!!errorAnchorEl}
        anchorEl={errorAnchorEl}
        style={{ zIndex: 11000 }}
        placement='top'
      >
        <TooltipContainer>
          <TooltipBlock>
            <TooltipInfo>
              {browser.os === 'android'
                ? translation('defaultProfileSettings.info.android')
                : translation('defaultProfileSettings.info.otherOS')}
            </TooltipInfo>
          </TooltipBlock>
        </TooltipContainer>
      </Popper>
    </Row>
  );
};

export default Resolutions;
