import { Radio, Select } from 'antd';
import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';

import { macArmOptions } from '../../../common/settings/browser/mac-arm-options';
import { SelectAdvanced } from '../../../updateProfile/components/overview-tab';
import { ITemplateFieldComponent } from '../interfaces/template-field-component.interface';
import { ParamContainer, RadioButton, SubTitle } from '../styles';

const Os: FC<ITemplateFieldComponent> = (props) => {
  const { browser, saveTemplate } = props;

  const { t: translation } = useTranslation();

  const osFull = [
    {
      display: 'Windows 10',
      value: 'win',
    },
    {
      display: 'Windows 11',
      value: 'win11',
    },
    {
      display: 'Mac Intel',
      value: 'mac',
    },
    {
      display: 'Mac ARM',
      value: 'macM1',
    },
    {
      display: 'Linux',
      value: 'lin',
    },
    {
      display: 'Android',
      value: 'android',
    },
    {
      display: 'Device OS',
      value: 'deviceOS',
    },
  ];

  const getOsSpecSelector = (): JSX.Element | null => {
    if (browser.os === 'macM1') {
      return (
        <SelectAdvanced
          value={browser.osSpec || 'M1'}
          onChange={(value: string): void => {
            saveTemplate({ osSpec: value, os: 'macM1' });
          }}
        >
          {macArmOptions.map((option) => (
            <Select.Option value={option} key={option}>
              {option}
            </Select.Option>
          ))}
        </SelectAdvanced>
      );
    }

    return null;
  };

  return (
    <ParamContainer>
      <SubTitle>
        {translation('defaultProfileSettings.os')}
      </SubTitle>
      <Radio.Group
        size='default'
        value={((): string => {
          if (browser.os === 'win' && browser.osSpec === 'win11') {
            return 'win11';
          }

          return browser.os;
        })()}
        onChange={(event): void => {
          let currentOsSpec = '';
          if (event.target.value === 'macM1') {
            currentOsSpec = 'M1';
          }

          if (event.target.value === 'win11') {
            currentOsSpec = 'win11';
          }

          saveTemplate({ os: event.target.value, osSpec: currentOsSpec });
        }}
      >
        {osFull.map(osOptions => (
          <RadioButton key={osOptions.value} value={osOptions.value}>
            {osOptions.display}
          </RadioButton>
        ))}
      </Radio.Group>
      {getOsSpecSelector()}
    </ParamContainer>
  );
};

export default Os;
