import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import { InputText } from './styles';
import { templatesContext } from '../../../../../../state';
import { ITemplateFieldComponent } from '../../../interfaces/template-field-component.interface';
import { Component, SubTitle } from '../../../styles';

const StartUrl: React.FC<ITemplateFieldComponent> = ({ browser, saveTemplate }) => {
  const templateCtx = useContext(templatesContext);
  const { t: translation } = useTranslation();

  const handleChange = (startUrl: string): void =>
    templateCtx.updateSelectedTemplate({
      ...templateCtx.selectedTemplate,
      browser: {
        ...templateCtx.selectedTemplate.browser,
        startUrl,
      },
    });

  return (
    <Component>
      <SubTitle>
        {translation('defaultProfileSettings.startUrl.title')}
      </SubTitle>
      <InputText
        placeholder={translation('newProfile.advanced.startUrl.inputPlaceholder') || ''}
        value={browser.startUrl}
        onChange={({ target }): void => handleChange(target.value)}
        onBlur={(): void => saveTemplate({ startUrl: browser.startUrl })}
      />
    </Component>
  );
};

export default StartUrl;
