import React, { FC, KeyboardEvent, useContext, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { ITemplate, templatesContext } from '../../../../state';
import { InputWithPopover } from '../../../../ui/modern-popover/input-with-popover';
import { ISlotInputInactive } from '../../../../ui/modern-popover/interfaces';
import { IRenderOptions, renderOptions } from '../../../../ui/modern-popover/options';
import { pasteSpintax } from '../../../../ui/modern-popover/paste-spintax';
import { getSpintaxList, ISpintaxList } from '../../../../ui/modern-popover/spintax-list';
import { createTemplate, updateTemplateBrowser } from '../api';
import { ITemplateFieldComponent } from '../interfaces/template-field-component.interface';
import { InputProfileName, ParamContainer, SubTitle } from '../styles';

const ProfileName: FC<ITemplateFieldComponent> = (props) => {
  const inputRef = useRef<HTMLInputElement | null>(null);

  const { updateSelectedTemplate, selectedTemplate } = useContext(templatesContext);

  const [profileName, setProfileName] = useState(selectedTemplate.profileName.dropFiles);
  const [isPopupSpintaxVisible, setIsPopupSpintaxVisible] = useState<boolean>(false);

  const { t: translation } = useTranslation();

  if (!NEW_FEATURES.templateProfileName) {
    return null;
  }

  const preparedTemplate = (dropFiles: string): ITemplate => ({
    ...selectedTemplate,
    profileName: {
      ...selectedTemplate.profileName,
      dropFiles,
    },
  });

  const changeName = (dropFiles: string): void => {
    setProfileName(dropFiles);
    const newTemplate = preparedTemplate(dropFiles);
    updateSelectedTemplate(newTemplate);
  };

  const saveName = async (): Promise<void> => {
    const dropFiles = selectedTemplate.profileName.dropFiles.trim();
    const newTemplate = preparedTemplate(dropFiles);
    if (selectedTemplate.id) {
      await updateTemplateBrowser(newTemplate);

      return;
    }

    const updatedTemplate = await createTemplate(newTemplate);
    updateSelectedTemplate(updatedTemplate);
  };

  const spintaxList: ISpintaxList[] = getSpintaxList(translation).filter(spintax => spintax.title !== '{profile name}');

  const onKeyPress = (event: KeyboardEvent<HTMLInputElement>): void => {
    if (event.key !== 'Enter') {
      return;
    }

    saveName();
  };

  const inputInactive = ({ onClick, autoFocus }: ISlotInputInactive): JSX.Element => (
    <InputProfileName
      ref={inputRef}
      value={profileName}
      placeholder={translation('defaultProfileSettings.auto') || 'Auto'}
      onChange={(event): void => changeName(event.target.value)}
      onKeyPress={(event): void => onKeyPress(event)}
      onClick={onClick}
      onBlur={saveName}
      autoFocus={autoFocus}
    />
  );

  const propsForRenderOptions: IRenderOptions = {
    profiles: [{}],
    nameFormat: profileName,
    translation,
    spintaxList,
    pasteSpintax: (newText: string) => pasteSpintax({ newText, inputElem: inputRef.current, setProfileName: changeName }),
  };

  return (
    <ParamContainer style={{ marginTop: 20 }}>
      <div style={{ marginBottom: 10 }}>
        <SubTitle>
          {translation('defaultProfileSettings.profileName')}
        </SubTitle>
      </div>
      <InputWithPopover
        popupSlot={(): JSX.Element => renderOptions(propsForRenderOptions)}
        slotInputInactive={inputInactive}
        anchorEl={inputRef.current}
        onCancel={saveName}
        needCancelMask={true}
        optionsVisible={isPopupSpintaxVisible}
        setOptionsVisible={setIsPopupSpintaxVisible}
      />
    </ParamContainer>
  );
};

export default ProfileName;
